h1 {
  color: white;
}
p {
  color: white;
  font-size: 20px;
  padding: 50px;
  font-family: "Poppins";
  letter-spacing: 1px;
  text-align: start;
}
.Portabout {
  font-size: 20px;
    padding: 5px;
    border-radius: 0px;
    background-color: #ffffff;
    color: rgb(0, 0, 0);
    border: none;
    font-family: "Poppins";
    width: 100px;
    font-weight: 700;
}
.Portabout:hover{
  font-size: 20px;
    padding: 5px;
    border-radius: 0px;
    background-color: #1d1d1d;
    color: rgb(255, 255, 255);
    border: 1px solid white;
    font-family: "Poppins";
    width: 100px;
    font-weight: 700;
}

@media (max-width: 320px) {
  .aboutdiv {
    background-image: url(./1.jpg);
    background-size: cover;
    background-repeat: repeat-x;
    width: -webkit-fill-available;
    height: 200vh;
    display: flex;
    flex-direction: column;

    text-align: center;
    color: white;
  }
  p {
    color: white;
    font-size: 12px;
    padding: 50px;
    font-family: "Poppins";
    letter-spacing: 1px;
    text-align: justify;
  }
  
}
